/// <reference types="@types/google.maps" />

import { CustomLocation, LocationLatLng } from "./customLocation.class";
import { CustomMap, MarkerType } from "./customMap.class";
import { OpenStreetMap } from "./openStreetMap.class";

import { debounceTime } from 'rxjs';
import { rubrics } from './rubrics';
 
let currentStatus = 'tabletennis';
const setStatusTo = async (modus: string) => {


  currentStatus = modus;



}


const initMap = async (): Promise<void> => {

  const myPosition: LocationLatLng = await CustomLocation.getCurrentPosition();


  setStatusTo('tabletennis');



  const myMap = new CustomMap("map", myPosition);


  myMap.mapsStatus.pipe(
    debounceTime(300)
  ).subscribe(async status => {

    const tableCoords: LocationLatLng[] = await OpenStreetMap.getCoordinatesForModus(status.center, status.zoom, currentStatus);


    tableCoords.forEach(table => {
      const markerType = rubrics[currentStatus];
      myMap.addMarker(table, markerType);
    })

  });



}


declare global {
  interface Window {
    initMap: () => void;
    setStatusTo: (status: string) => void;
  }
}
window.initMap = initMap;
window.setStatusTo = setStatusTo;



