import { LocationLatLng } from "./customLocation.class";
import { ReplaySubject} from 'rxjs';

interface MapStatus {
    center: LocationLatLng | any;
    zoom: number | undefined; 
}

export interface MarkerType {
    text: string;
    color: string;
}

const googleMapScales = {
    19 : 1128.497220,
    18 : 2256.994440,
    17 : 4513.988880,
    16 : 9027.977761,
    15 : 18055.955520,
    14 : 36111.911040,
    13 : 72223.822090,
    12 : 144447.644200,
    11 : 288895.288400,
    10 : 577790.576700,
    9 : 1155581.153000,
    8  : 2311162.307000,
    7  : 4622324.614000,
    6  : 9244649.227000,
    5  : 18489298.450000,
    4  : 36978596.910000,
    3  : 73957193.820000,
    2  : 147914387.600000,
    1  : 295828775.300000,
    0  : 591657550.500000,
} 

export class CustomMap {

    private map: google.maps.Map;

    public mapsStatus: ReplaySubject<MapStatus>;

    constructor(mapDiv: string, location: LocationLatLng) {

        const styles: google.maps.MapTypeStyle[] = [{
            featureType: "poi",
            elementType: "labels",
            stylers: [{
                visibility: "off"
              }]    
        }]

        const element = document.getElementById(mapDiv) as HTMLElement;
        this.map = new google.maps.Map(element, {center: location, zoom: 15, maxZoom: 17, minZoom: 14,  styles});

        this.mapsStatus = new ReplaySubject(1);
        this.map.addListener('center_changed', () => {
            this.mapsStatus.next({
                center: {
                    lat: this.map.getCenter()?.lat(), 
                    lng: this.map.getCenter()?.lng()
                },
                zoom: googleMapScales[this.map.getZoom() as number] / 20,
            });
        });
        this.map.addListener('zoom_changed', () => {
            this.mapsStatus.next({
                center: {
                    lat: this.map.getCenter()?.lat(), 
                    lng: this.map.getCenter()?.lng()
                },
                zoom: googleMapScales[this.map.getZoom() as number] / 20,
            });
        });
        
    }

 

    addMarker(location: LocationLatLng, markerType: MarkerType): void  {
        if (this.map) {


            const marker = new google.maps.Marker({
                position: location,
                map: this.map,
                label: {
                    text: markerType.text,                    
                },
                
            })
            marker.setIcon(`http://maps.google.com/mapfiles/ms/icons/${markerType.color}-dot.png`)
        }
    }
}